body {
  margin: 0;
  a {
    text-decoration: none;
    color: inherit; } }

footer {
  position: fixed;
  left: 0px;
  bottom: 0px;
  height: 50px;
  width: 100%; }

.content {
  display: table;
  width: 100%;
  height: 60vh;
  margin: 0; }

.dados {
  display: table-cell;
  margin: auto;
  vertical-align: middle;
  height: 30px;
  top: 50%;
  margin-top: -15px; }

.contact {
  li {
    list-style: none; } }

.user {
  &__content {
    // background-color: #fafafa
    padding: 15px 0; }
  &__jobs {
    margin: 0;
    padding: 0 0 0 10px;
    list-style: none; }
  &__avatar {
    box-shadow: 2px 2px 0px rgba(0, 0, 0, 0.05);
    border-radius: 10px;
    width: 80px;
    height: auto;
    transition: all 0.3s ease-in;
    float: left;
    margin: 0 10px;
    &:hover {
      box-shadow: 0px 2px 30px rgba(0, 0, 0, 0.15), 0px 2px 10px rgba(0, 0, 0, 0.15);
      transition: all 0.3s ease-in; } } }

.navegation {
  background-color: white;
  padding: 10px;
  box-shadow: 2px 2px 3px rgba(0, 0, 0, 0.20);
  z-index: 1000; }
